@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-gray-950 text-gray-100 antialiased;
  }
  
  * {
    @apply border-border-light;
  }
  
  /* Custom scrollbar */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    @apply bg-transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    @apply bg-gray-700 rounded-full;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    @apply bg-gray-600;
  }
}

@layer components {
  .glass {
    @apply bg-glass backdrop-blur-md border border-border-light rounded-xl;
  }
  
  .glass-dark {
    @apply bg-glass-dark backdrop-blur-md border border-border-light rounded-xl;
  }
  
  .btn-primary {
    @apply px-6 py-2.5 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium transition-all duration-200 shadow-lg hover:shadow-xl;
  }
  
  .btn-secondary {
    @apply px-6 py-2.5 bg-glass hover:bg-glass-light border border-border-light text-gray-200 rounded-lg font-medium transition-all duration-200;
  }
  
  .severity-badge {
    @apply px-3 py-1 text-xs font-semibold rounded-full;
  }
  
  .severity-critical {
    @apply bg-red-500/20 text-red-400 border border-red-500/30;
  }
  
  .severity-high {
    @apply bg-orange-500/20 text-orange-400 border border-orange-500/30;
  }
  
  .severity-medium {
    @apply bg-yellow-500/20 text-yellow-400 border border-yellow-500/30;
  }
  
  .severity-low {
    @apply bg-green-500/20 text-green-400 border border-green-500/30;
  }
}